// IMPORTANT: this is a plugin which requires jQuery for initialisation and data manipulation

import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { Http, Response, RequestOptions, Headers } from "@angular/http";
import { Observable } from "rxjs/Observable";
import { Router, CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, ActivatedRoute, Params } from '@angular/router';
import "rxjs/Rx";
import { CommonService } from "../services/commonService";
import { UserService } from "../services/userService";
import { DivisionService } from "../services/divisionService";
import { JobsService } from "../services/jobsService";
import * as $ from 'jquery';
declare const jQuery: any;
import 'fullcalendar';
import 'fullcalendar-scheduler';
import 'jqueryui';
declare const swal: any;

@Component({
    selector: 'app-calendar-cmp',
    templateUrl: 'calendar.component.html',
    styleUrls: ['./calendar.component.scss'],
    providers: [CommonService, DivisionService, UserService, JobsService]
})

export class CalendarComponent implements OnInit {
    public calendarForm = new FormGroup({
        divChk: new FormControl()
    });
    public divisionList: any;
    public autoUsers: any;
    public userProfile: any;
    public jobData: any[] = [];
    public workOrdNo: any;

    constructor(
        private http: Http,
        private router: Router,
        private formBuilder: FormBuilder,
        private commonService: CommonService,
        private divisionService: DivisionService,
        private activatedRoute: ActivatedRoute,
        private userService: UserService,
        private jobService: JobsService,
    ) { }

    ngOnInit() {
        if (JSON.parse(sessionStorage.getItem('LogEmployee')) != null) {
            this.userProfile = JSON.parse(sessionStorage.getItem('LogEmployee'));
            this.getAutocompleteUser();
            this.GetAllDivisions();
            this.getJobDetailForCalendar();
            var self = this;
            $(document).ready(function () {
                $(".panel-toogle[data-toggle='toggle']").on('click', function () {
                    var selector = $(this).data("target");
                    $(".right-sidebar-panel:not('" + selector + "')").each(function () {
                        $(this).removeClass("in");
                    });
                    $(selector).toggleClass('in');

                });

                $(document).on('click', '.fc-cell-text', function () {
                    self.getWorkOrderDetails($(this).text());
                    jQuery('#myModal').modal('toggle');
                });

            });

            //var jobTable = JSON.parse(localStorage.getItem("JobData"));

            $(function () {
                $('#external-events .fc-event').each(function () {
                    $(this).data('event', {
                        title: $.trim($(this).text()),
                        stick: true
                    });

                    $(this).draggable({
                        zIndex: 999,
                        revert: true,
                        revertDuration: 0
                    });

                });

            });
            //    $('.loader').show();
            //    (<any>$('#calendar')).fullCalendar({
            //        schedulerLicenseKey: 'CC-Attribution-NonCommercial-NoDerivatives',
            //        editable: true,
            //        droppable: true,
            //        aspectRatio: 1.2,
            //        scrollTime: '06:00',
            //        header: {
            //            left: 'today prev,next',
            //            center: 'title',
            //            right: 'timelineDay,agendaWeek,month'
            //        },
            //        defaultView: 'timelineDay',
            //        resourceLabelText: 'Jobs',
            //        resourceGroupField: 'jobnum',
            //        resources: jobTable,
            //        events: [
            //            //{ id: '1', resourceId: 'b', start: '2018-07-12T02:00:00', end: '2018-02-07T07:00:00', title: 'event 1' },
            //            //{ id: '2', resourceId: 'c', start: '2018-02-07T05:00:00', end: '2018-02-07T22:00:00', title: 'event 2' },
            //            //{ id: '3', resourceId: 'd', start: '2018-02-06', end: '2018-02-08', title: 'event 3' },
            //            //{ id: '4', resourceId: 'e', start: '2018-02-07T03:00:00', end: '2018-02-07T08:00:00', title: 'event 4' },
            //            //{ id: '5', resourceId: 'f', start: '2018-02-07T00:30:00', end: '2018-02-07T02:30:00', title: 'event 5' }
            //        ],
            //        drop: function (date) {
            //            if ($('#drop-remove').is(':checked')) {
            //                $(this).remove();
            //            }
            //        },
            //        eventReceive: function (event) {
            //            console.log('eventReceive', event);
            //        },
            //        eventDrop: function (event) {
            //            console.log('eventDrop', event);
            //        }
            //    });
            //    $('.loader').hide();
            //});
        }
        else {
            this.router.navigate(['pages/login']);
        }
    }

    getAutocompleteUser() {
        $('.loader').show();
        this.userService.GetRoleBasedUser(this.commonService.baseApiUrl, this.userProfile.ID).subscribe(
            data => {
                this.autoUsers = null;
                var dataUsers = JSON.stringify(data);
                var userParse = JSON.parse(dataUsers);
                var userInfo = JSON.parse(userParse["_body"]);
                if (userInfo != null) {
                    this.autoUsers = userInfo;
                    $('.loader').hide();
                }
            });
    }

    getJobDetailForCalendar() {
        $('.loader').show();
        this.jobService.GetJobDetailForCalendar(this.commonService.baseApiUrl, this.userProfile.ID).subscribe(
            data => {
                this.jobData = [];
                //localStorage.removeItem("JobData");
                var dataUsers = JSON.stringify(data);
                var userParse = JSON.parse(dataUsers);
                var userInfo = JSON.parse(userParse["_body"]);
                if (userInfo != null) {
                    for (let job of userInfo) {
                        var jobJson = { id: job.WorkOrdID, jobnum: job.JobName, title: job.WorkOrderNo, eventColor: job.ColorCode }
                        this.jobData.push(jobJson);
                    }
                    var jsonObj = JSON.stringify(this.jobData);
                    var jsonData = JSON.parse(jsonObj);
                    $(function () {
                        $('#external-events .fc-event').each(function () {
                            $(this).data('event', {
                                title: $.trim($(this).text()),
                                stick: true
                            });

                            $(this).draggable({
                                zIndex: 999,
                                revert: true,
                                revertDuration: 0
                            });

                        });
                        //console.log(jsonData);
                        $('.loader').show();
                        (<any>$('#calendar')).fullCalendar({
                            schedulerLicenseKey: 'CC-Attribution-NonCommercial-NoDerivatives',
                            editable: true,
                            droppable: true,
                            aspectRatio: 1.2,
                            scrollTime: '06:00',
                            header: {
                                left: 'today prev,next',
                                center: 'title',
                                right: 'timelineDay,agendaWeek,month'
                            },
                            defaultView: 'timelineDay',
                            resourceLabelText: 'Jobs',
                            resourceGroupField: 'jobnum',
                            resources: jsonData,//[{id: 'a', jobnum: '23405 - Impact Homes', title: '23405-001', eventColor: 'green', children: [{ id: 'a1', title: 'Room A1' }] }],
                            events: [
                                //{ id: '1', resourceId: '17901', start: '2018-07-18T02:00:00', end: '2018-02-07T07:00:00', title: 'WO20862 - 0001' },
                                //{ id: '2', resourceId: 'c', start: '2018-02-07T05:00:00', end: '2018-02-07T22:00:00', title: 'event 2' },
                                //{ id: '3', resourceId: 'd', start: '2018-02-06', end: '2018-02-08', title: 'event 3' },
                                //{ id: '4', resourceId: 'e', start: '2018-02-07T03:00:00', end: '2018-02-07T08:00:00', title: 'event 4' },
                                //{ id: '5', resourceId: 'f', start: '2018-02-07T00:30:00', end: '2018-02-07T02:30:00', title: 'event 5' }
                            ],
                            drop: function (date) {
                                if ($('#drop-remove').is(':checked')) {
                                    $(this).remove();
                                }
                            },
                            eventReceive: function (event) {
                                console.log('eventReceive', event);
                            },
                            eventDrop: function (event) {
                                console.log('eventDrop', event);
                            }
                        });
                        $('.loader').hide();
                    });
                    //localStorage.setItem("JobData", JSON.stringify(this.jobData));
                    //$('.loader').hide();
                }
            });
    }

    GetAllDivisions() {
        $('.loader').show();
        this.divisionService.GetAllDivisions(this.commonService.baseApiUrl).subscribe(
            data => {
                var dataDiv = JSON.stringify(data);
                var divParse = JSON.parse(dataDiv);
                var divList = JSON.parse(divParse["_body"]);
                if (divList != null) {
                    this.divisionList = divList;
                }
            });
    }
    getWorkOrderDetails(woNumber: any) {
        this.workOrdNo = woNumber;
    }
}



